@import './mixins';
@import './variables';


body{
   font-family: 'Nunito', sans-serif;
   font-weight: 400;
   font-style: normal;
   font-size: 16px;
   line-height: 26px;
   color: rgba(0, 0, 0, 0.87);
   background: #E5E5E5;
}

.d-f{
   display: flex;
}

.d-g{
   display: grid;
}

.jc-sb{
   justify-content: space-between;
}

.jc-c{
   justify-content: center;
}

.ai-c{
   align-items: center;
}

.fd-c{
   flex-direction:column;
}

.fw-w{
   flex-wrap: wrap;
}


.ta-c{
   text-align: center;
}

.ta-e{
   text-align: end;
}

.gtc-3{
   grid-template-columns: repeat(auto-fit,minmax(275px,1fr));
}

.section{
   margin-bottom: 140px;
}

.title{
   font-size: 40px;
   line-height: 40px;
}

.btn-yellow{
   background: $yellow;

   &:hover{
      background: #fdf08a;
   }
}

.btn-gray-light{
   background: #B4B4B4;  
   color: rgba(255, 255, 255, 0.87);

   &:hover{
      background: gray;
   }
}
